  font-family: 'Bona Nova SC';
  font-weight: 400;
  font-style: normal;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
}

:root {
  --headerText: white;
  --mainColor: blue;
  --buttonBackgroundcolor: rgb(172, 211, 255);
  --dialogColor: rgba(71, 94, 141, 0.9);
}

* {
  padding: 0rem;
  margin: 0rem;
}

html {
  font-size: 62.5%;
}

header {
  background-color: var(--mainColor);
  display: flex;
  justify-content: center;
}

header div h1 {
  font-family: 'Bona Nova SC', sans-serif;
  letter-spacing: 0.5rem;
  color: var(--headerText);
  font-size: 6.5rem;
  padding: 1rem;
}

table {
  display: table;
  border-collapse: separate;
  border-spacing: 0rem;
}

td,
th {
  width: 4rem;
  height: 4rem;
  border: 0.5px solid var(--mainColor);
}
th {
  font-family: 'Bona Nova SC', sans-serif;
  font-size: 1.5rem;
  border-color: white;
}

main div {
  font-family: 'Bona Nova SC', sans-serif;
  display: grid;
  justify-items: center;
}

main div p {
  font-size: 2rem;
}

.main {
  margin-top: 1rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(452px, 1fr));
}

.buttons {
  margin-top: 2rem;
  display: grid;
  gap: 1rem;
}

button {
  font-family: 'Bona Nova SC', sans-serif;
  font-size: 1.5rem;
  padding: 1rem;
  background-color: var(--buttonBackgroundcolor);
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: rgb(125, 150, 180);
}

dialog {
  width: 50rem;
  height: 50rem;
  border: none;
  background-color: var(--dialogColor);
  display: grid;
  align-content: center;
  text-align: center;
  font-family: 'Bona Nova SC', sans-serif;
  gap: 5rem;
  border: 1px solid #ccc;
  padding: 1rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
}

dialog h1 {
  font-size: 10rem;
  letter-spacing: 1.5rem;
}

dialog button {
  word-spacing: 0.5rem;
  font-size: 2rem;
}
